<template>
  <div>
    <div class="header">
      <Header title="通関処理状況"/>
    </div>
    <div class="conatiner">
      <div class ="metrics"><span>平均STS 50/55 滞留時間 </span>
      <span v-if="this.sakuseiDuration == 0.0">N/A</span>
      <span v-else>{{this.sakuseiDuration.toFixed(0)}}</span>
      <span>分</span></div>
      <div class ="metrics"><span>平均STS 70 滞留時間 </span>
      <span v-if="this.shinsaDuration == 0.0">N/A</span>
      <span v-else>{{this.shinsaDuration.toFixed(0)}}</span>
      <span>分</span></div>
      <div>
        <span v-for="user in this.users" :key="user">
          <span v-if="user == this.currentUser" class="s-button" v-on:click="OnclickUserButton(user)">{{user}}</span>
          <span v-else class="c-button" v-on:click="OnclickUserButton(user)">{{user}}</span>
        </span>
      </div>
      <div>
        <span v-for="stscode in this.statuscodes" :key="stscode">
          <span v-if="stscode == this.currentSTS" class="s-button" v-on:click="OnclickStatusButton(stscode)">{{stscode}}
            <img v-if="stscode.padStart(2,'0') == '03'" src="./assets/marioicons/03.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '20'" src="./assets/marioicons/20.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '25'" src="./assets/marioicons/25.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '40'" src="./assets/marioicons/40.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '50'" src="./assets/marioicons/50.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '55'" src="./assets/marioicons/55.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '65'" src="./assets/marioicons/65.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '70'" src="./assets/marioicons/70.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '72'" src="./assets/marioicons/72.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '75'" src="./assets/marioicons/75.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '80'" src="./assets/marioicons/80.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '90'" src="./assets/marioicons/90.png"/>
            <img v-else src="./assets/marioicons/NA.png"/>
          </span>
          <span v-else class="c-button" v-on:click="OnclickStatusButton(stscode)">{{stscode}}
            <img v-if="stscode.padStart(2,'0') == '03'" src="./assets/marioicons/03.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '20'" src="./assets/marioicons/20.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '25'" src="./assets/marioicons/25.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '40'" src="./assets/marioicons/40.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '50'" src="./assets/marioicons/50.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '55'" src="./assets/marioicons/55.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '65'" src="./assets/marioicons/65.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '70'" src="./assets/marioicons/70.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '72'" src="./assets/marioicons/72.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '75'" src="./assets/marioicons/75.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '80'" src="./assets/marioicons/80.png"/>
            <img v-else-if="stscode.padStart(2,'0') == '90'" src="./assets/marioicons/90.png"/>
            <img v-else src="./assets/marioicons/NA.png"/>
          </span>
        </span>
      </div>
      <div>
        <p>件数:{{this.ttl.toString()}}</p>
      </div>
      <infinite-scroll @infinite-scroll="loadDataFromServer" :noResult="noResult">
        <div class="status-table">
          <TimetableHeader :times="times"/>
          <Status v-for="awb in awbs" :key="awb" :stss="sts[awb]" :awbno="awb"/>
        </div>
      </infinite-scroll>
    </div>
  </div>
</template>
<script>
import Header from './components/Header.vue'
import Status from './components/Status.vue'
import TimetableHeader from './components/TimetableHeader.vue'
import InfiniteScroll from 'infinite-loading-vue3'

export default {
  name: 'App',
  components: {
    Header,
    Status,
    TimetableHeader,
    InfiniteScroll,
  },
  data() {
    return {
      awbs: [],
      sts:{},
      times:[],
      users:[],
      statuscodes:[],
      ttl:0,
      currentPage:0,
      par:25,
      from:0,
      to:0,
      latest: 0,
      lastUpdate: 0,
      noResult:Boolean,
      currentUser:"",
      currentSTS:"",
      $host:"",
      sakuseiDuration:0.0,
      shinsaDuration:0.0,
    }
  },
  methods:{
    async OnclickUserButton(user){
      this.SelectUser(user)
      this.Refresh()
    },
    async OnclickStatusButton(sts){
      this.SelectSTS(sts)
      this.Refresh()
    },
    async Refresh(){
      this.awbs = []
      this.sts = {}
      this.to = Math.trunc(((new Date()).getTime() + 3600000 - 1) / 3600000) * 3600000
      this.times = await this.fetchTimes(this.from,this.to)
      this.fetchAwbs(0,25,this.currentUser,this.currentSTS)
    },
    SelectSTS(sts){
      if(this.currentSTS == sts) this.currentSTS = ""
      else{
        this.currentSTS = sts
      }
    },
    SelectUser(user){
      if(this.currentUser == user) this.currentUser = ""
      else{
        this.currentUser = user
      }
    },  
    async UpdateMetrics(){
          const targetUrl = `http://` + this.$host + `/api/metrics`
          const res = await fetch(targetUrl);
          const met = await res.json()
          if(met != null){
            if(met.sakucnt != 0){
              this.sakuseiDuration = met.sakuttl / met.sakucnt
            }else{
              this.sakuseiDuration = 0.0
            }
            if(met.shincnt != 0){
              this.shinsaDuration = met.shinttl / met.shincnt
            }else{
              this.shinsaDuration = 0.0
            }
          }
    },
    async UpdateStatus(){
      const from = Math.trunc((new Date()).getTime() / (10 * 60 * 1000)) * 10 * 60 * 1000
      if(this.to == from){
        this.times = []
        const now = (new Date()).getTime()
        const to = Math.trunc((now + 3600000 - 1) / 3600000) * 3600000
        this.times = await this.fetchTimes(this.from,to,false)
        this.to = to
      }
      const params = {from : from,to: this.to,timespan:10,islatest:"true"};
      const query = new URLSearchParams(params);
      const targetUrl = `http://` + this.$host + `/api/timeline?`
      const res = await fetch(targetUrl+`${query}`);
      const timeres = await res.json()
      const latestTL = timeres.timeline
      var cntr = 0
      var offset = -1
      for(let t in this.times){
        if(this.times[t].time == latestTL[0].time){
          offset = cntr
          break;
        }
        cntr++
      }
      cntr=0
      if(offset < 0){
        offset = this.times.length
      }
      cntr=0
      for(let t in latestTL){
        if(offset + cntr == this.times.length){
          this.times.push({time:latestTL[t].time, index:offset + cntr})
        }
        cntr++
      }
      for(let a in this.awbs){
        const params = {key : this.awbs[a],from:from,to:this.to,islatest:"true"};
        const query = new URLSearchParams(params);
        const targetUrl = `http://` + this.$host + `/api/status?`
        const res = await fetch(targetUrl + `${query}`);
        const sts = await res.json();
        const stss = sts.status;
        cntr = 0
        if(this.sts[this.awbs[a]] == undefined){
          continue
        }
        for(let s in stss){
          let sts = stss[s]
          let awbno = sts.awbno
          if(offset + cntr == this.sts[awbno].length){
            this.sts[sts.awbno].push({
              index:offset+s,
              awbno:awbno,
              base_time:latestTL[s].time,
              time_span:10,
              q:sts.q,
              status_code:sts.status_code,
              section_code:sts.section_code,
              company_code:sts.company_code,
              company_name:sts.company_name,
              last_updated_user:sts.last_updated_user,
              last_updated_id:sts.last_updated_id,
              is_stocked:sts.is_stocked,
              igs_status:sts.igs_status,
            })
          }else{
            this.sts[sts.awbno][offset+cntr].index = offset+cntr
            this.sts[sts.awbno][offset+cntr].awbno =awbno
            this.sts[sts.awbno][offset+cntr].base_time =latestTL[s].time
            this.sts[sts.awbno][offset+cntr].time_span =10
            this.sts[sts.awbno][offset+cntr].q=sts.q
            this.sts[sts.awbno][offset+cntr].status_code=sts.status_code
            this.sts[sts.awbno][offset+cntr].section_code=sts.section_code
            this.sts[sts.awbno][offset+cntr].company_code=sts.company_code
            this.sts[sts.awbno][offset+cntr].company_name=sts.company_name
            this.sts[sts.awbno][offset+cntr].last_updated_user=sts.last_updated_user
            this.sts[sts.awbno][offset+cntr].last_updated_id=sts.last_updated_id
            this.sts[sts.awbno][offset+cntr].is_stocked=sts.is_stocked
            this.sts[sts.awbno][offset+cntr].igs_status=sts.igs_status
          }
          cntr++
        }
      }
    },
    async UpdateAwbs(){
      this.fetchLatestAwbs(this.currentUser,this.currentSTS, true)
      this.lastUpdate = (new Date()).getTime()
    },
    async UpdateStatusCodes(){
      this.loadStatusCodes()
    },
    async loadUsers(){
      const res = await fetch(`http://` + this.$host + `/api/user`);
      const users = await res.json()
      this.users = users.users        
    },
    async loadStatusCodes(){
      const res = await fetch(`http://`+ this.$host +`/api/stslist`);
      const stscodes = await res.json()
      this.statuscodes = stscodes.statuscodes        
    },
    async loadDataFromServer(){
      if(this.currentPage*this.par < this.ttl) {
        this.fetchAwbs(this.currentPage,this.par,this.currentUser,this.currentSTS,true)
      }else{
        this.noResult = true
      }
    },
    async fetchLatestAwbs(user = "",sts ="", isupdate){
        const params = {user:user,sts:sts,isupdate:true,lastupdated:this.lastUpdate};
        const query = new URLSearchParams(params);
        const targetUrl = `http://` + this.$host + `/api/awb?`
        const res = await fetch(targetUrl +`${query}`);
        const awbres = await res.json()
        const awbs = awbres.awbnos
        if(awbres != null){
          this.ttl += awbres.ttl
        }
        for(let a in awbs){
          if(awbs[a] in this.awbs === false){
            this.awbs.push(awbs[a])
          }
        }
        for(let a in awbs){
          let sts = await this.fetchStatus(awbs[a],this.from,this.to, isupdate)
          this.sts[awbs[a].toString()] = sts
        }
    },
    async fetchAwbs(page = 0, par = 25,user = "",sts ="", isupdate = false){
        this.currentPage = page + 1
        const params = {sort : "awbno" ,page : page, par:par,user:user,sts:sts};
        const query = new URLSearchParams(params);
        const targetUrl = `http://` + this.$host + `/api/awb?`
        const res = await fetch(targetUrl +`${query}`);
        const awbres = await res.json()
        const awbs = awbres.awbnos
        if(awbres != null){
          this.ttl = awbres.ttl
        }
        for(let a in awbs){
          this.awbs.push(awbs[a])
        }
        for(let a in awbs){
          let sts = await this.fetchStatus(awbs[a],this.from,this.to, isupdate)
          this.sts[awbs[a].toString()] = sts
        }
    },
    async fetchStatus(awb,from,to, isupdate = false){
      const params = {key : awb,from:from,to:to, isupdate:isupdate};
      const query = new URLSearchParams(params);
      const targetUrl = `http://` + this.$host + `/api/status?`
      const res =await fetch(targetUrl + `${query}`);
      const sts = await res.json();
      const array = sts.status;
      return array;
    },
    async fetchTimes(from,to,islatest = false){
      var islateststr = "false"
      if(islatest) islateststr = "true"
      const params = {from : from,to:to,timespan:10,islatest:islateststr};
      const query = new URLSearchParams(params);
      const targetUrl = `http://` + this.$host + `/api/timeline?`
      const res = await fetch(targetUrl+`${query}`);
      const timeres = await res.json();
      const timeArray = timeres.timeline;
      return timeArray;
    }
  },
  async created(){
    this.$host = location.host
    setInterval(this.UpdateStatus,60000)
    setInterval(this.UpdateAwbs,600000)
    setInterval(this.UpdateStatusCodes,300000)
    setInterval(this.UpdateMetrics,10000)
    this.loadUsers()
    this.loadStatusCodes()
    const now = (new Date()).getTime()
    this.lastUpdate = now
    const from = now - 5 *3600000
    const to = Math.trunc((now + 3600000 - 1) / 3600000) * 3600000
    this.from = from
    this.to = to
    const times = await this.fetchTimes(from,to);
    this.times = times
    this.fetchAwbs()
  }
}
</script>

